input[type="radio"] {
  float: left;
  margin-right: 80px;
  color: blue;
}

.answer {
  margin-right: 50px;
  display: block;
}

.true-answer {
  color: rgb(33, 190, 54);
  font-weight: bold;
}

.false-answer {
  color: red;
  font-weight: bold;
}

.oneQuestion {
  margin-bottom: 10px;
}
@media only screen and (max-width: 480px) {
    .radio {
      display: block;
      height: 30px;
      line-height: 30px;
    }
}